// React 기본 설정
import React from 'react';
import ReactDOM from 'react-dom';

// 글로벌 설정
global.__DEV__ = true;
global.window = global.window || {};
global.React = React;
global.ReactDOM = ReactDOM;

// 콘솔 메시지 필터링
const originalConsoleError = console.error;
console.error = (...args) => {
  const errorMessage = args[0] || '';
  
  // React 내부 오류 무시
  if (errorMessage && typeof errorMessage === 'string' && 
      (errorMessage.includes('ReactCurrentDispatcher') || 
       errorMessage.includes('ReactCurrentOwner') ||
       errorMessage.includes('act(') ||
       errorMessage.includes('inside a test was not wrapped in act') ||
       errorMessage.includes('DevMenu could not be found'))) {
    return;
  }
  
  originalConsoleError(...args);
};

// 경고 필터링에 DevMenu 관련 경고 추가
const originalConsoleWarn = console.warn;
console.warn = (...args) => {
  const warningMessage = args[0] || '';
  
  const ignoredWarnings = [
    'ProgressBarAndroid has been extracted',
    'Clipboard has been extracted',
    'TurboModuleRegistry.getEnforcing',
    'DevMenu could not be found',
    'DevSettings',
  ];
  
  if (ignoredWarnings.some(warning => 
    typeof warningMessage === 'string' && warningMessage.includes(warning)
  )) {
    return;
  }
  
  originalConsoleWarn(...args);
};

